.button {
  color: black;
  font-size: 16px;
  width: 70px;
  height: 35px;
  cursor: pointer;
  background-color: lightgrey;
  border: none;
  border-radius: 10px;
  outline: none;
  transition: all 0.2s;
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  }
  &:active {
    transform: translateY(-1px);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }
}

.button.text {
  width: 70px;
  font-size: 13px;
  font-weight: bold;
  margin-left: 5px;
}
.button.text.active {
  background-color: lightskyblue;
  color: blue;
}
.button.logout {
  position: absolute;
  top: 0;
  right: 50px;
  background-color: rgba(44, 130, 201, 1);
  @media screen and (max-width: 991px) {
    top: -60px;
  }
  @media screen and (max-width: 768px) {
    top: -80px;
    right: 20px;
  }
}
